<template>
  <main-header></main-header>
  <div class="home">
    <spinner v-if="loading" :spinnerColor="'magenta'"></spinner>
    <div class="error" v-else-if="error">
      <h2>Error</h2>
    </div>
  </div>
</template>

<style scoped>
.home {
  width: 100%;
  padding: 2rem 0;
}
.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
</style>

<script>
import axios from "axios";
import Spinner from "@/components/Spinner.vue";
import MainHeader from "@/components/Header.vue";

export default {
  name: "Home",
  async mounted() {
    try {
      const randomShirt = await this.randomShirt();
      const { isGradient } = randomShirt;

      const route = {
        name: isGradient ? "gradient" : "colored",
        params: {
          id: randomShirt.id,
        },
      };
      this.$router.push(route);
    } catch (err) {
      console.log("Error getting a random shirt");
      console.log("Error:", err);
      this.error = true;
      this.loading = false;
    }
  },
  data() {
    return {
      loading: true,
      error: false,
    };
  },
  methods: {
    async randomShirt() {
      const isGradient = Math.round(Math.random() * 200) >= 100;
      const shirtCount = isGradient
        ? await this.getShirtCount()
        : await this.getGradientShirtCount();
      const randomId = Math.round(Math.random() * shirtCount);
      const shirt = {
        id: randomId <= 0 ? 1 : randomId,
        isGradient,
      };
      return shirt;
    },
    async getShirtCount() {
      const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
      const query = `
        query getShirtCount {
          count: shirtCount
        }
      `;
      const body = {
        query,
      };
      const response = (await axios.post(endpoint, body)).data;
      const { count } = response.data;
      return count;
    },
    async getGradientShirtCount() {
      const endpoint = "https://shirt-graphql-server.herokuapp.com/graph";
      const query = `
        query getGradientShirtCount {
          count: gradientShirtCount
        }
      `;
      const body = {
        query,
      };
      const response = (await axios.post(endpoint, body)).data;
      const { count } = response.data;
      return count;
    },
  },
  components: {
    MainHeader,
    Spinner,
  },
};
</script>
