<template>
  <div class="header center">
    <div class="searchBar center">
      <input type="text" class="searchInput" v-model="shirtId" />
      <button class="searchButton" @click="search">Search</button>
      <select name="shirtType" id="" v-model="type">
        <option value="colored">Colored</option>
        <option value="gradient">Gradient</option>
      </select>
    </div>
    <nav class="nav">
      <router-link to="/"> Home </router-link>
      <router-link to="/listing"> Listing </router-link>
      <router-link to="/create"> Create </router-link>
    </nav>
  </div>
</template>

<style scoped>
.header {
  background: rgba(0, 0, 0, 0.6);
  padding: 1rem 0;
  height: clamp(50px, 10vh, 80px);
  position: relative;
}
.nav {
  position: absolute;
  right: 5rem;
}
.nav a {
  color: hsl(0, 0%, 70%);
  font-weight: 600;
  text-decoration: none;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0 0.5rem;
}
.nav a.router-link-active,
.nav a.router-link-active {
  color: hsl(0, 0%, 95%);
}

.searchBar {
  height: 100%;
}
.searchInput,
.searchButton {
  border: none;
  height: 50%;
}
.searchButton {
  border-left: 1px solid gray;
}
.searchButton:hover {
  filter: brightness(75%);
  cursor: pointer;
}
</style>

<script>
export default {
  name: "Header",
  data() {
    return {
      shirtId: "",
      type: "colored",
    };
  },
  methods: {
    search() {
      const isValid = this.validateString(this.shirtId);
      if (!isValid) return;
      this.$router.push({ name: this.type, params: { id: this.shirtId } });
    },
    // Checks if the query only has numbers
    validateString(query) {
      const parts = query.split("");
      return parts.every((char) => Number.parseInt(char));
    },
  },
};
</script>
